<template>
  <v-container>
    <v-sheet color="rgba(0,0,0,0.5)" rounded>
      <v-row class="text-center">
        <v-col cols="12">
          <span class="white--text text-h6 text-md-h4"
            >Registro De Entradas Y Salidas</span
          >
        </v-col>
      </v-row>
      <v-row class="text-center">
        <v-col cols="12" md="3">
          <v-col cols="12">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="inicio"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-combobox
                  v-model="inicio"
                  chips
                  small-chips
                  label="Fecha Inicio"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  dark
                  clearable
                ></v-combobox>
              </template>
              <v-date-picker v-model="inicio" no-title scrollable dark :max="maximo">
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="menu = false">
                  Cancelar
                </v-btn>
                <v-btn text color="primary" @click="$refs.menu.save(inicio)">
                  OK
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
          <v-col cols="12" v-if="inicio">
            
            <v-menu
              ref="menu1"
              v-model="menu1"
              :close-on-content-click="false"
              :return-value.sync="final"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-combobox
                  v-model="final"
                  chips
                  small-chips
                  label="Fecha Final"
                  prepend-icon="mdi-calendar"
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  dark
                  clearable
                ></v-combobox>
              </template>
              
              <v-date-picker v-model="final" no-title scrollable dark :min="inicio" :max="maximo">
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="menu1 = false">
                  Cancelar
                </v-btn>
                <v-btn text color="primary" @click="$refs.menu1.save(final); Registros=[];Cargar_Datos()">
                  OK
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>
        </v-col>

        <v-col cols="12" md="9" v-if="inicio && final">
          <v-data-table
            :headers="headers"
            :items="Registros"
            :loading="loading"
            class="elevation-1"            
            dark
          ></v-data-table>
        </v-col>
      </v-row>
    </v-sheet>
  </v-container>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import axios from "axios";

export default {
    props:{
        no_trabajador:{
            default:null
        }
    },
  computed: {
    ...mapState(["ip"]),
  },
  mounted() {
    this.Checar_Token()
  },
  data() {
    return {
      Registros: [],
      loading: true,
      headers: [
        {
          text: "Número De Empleado",
          align: "start",
          sortable: true,
          value: "No_empleado",
        },
        {
          text: "Docente",

          sortable: true,
          value: "Nombre",
        },
        { text: "Fecha", value: "Fecha" },
        { text: "Hora", value: "Hora" },
        { text: "Tipo", value: "Tipo" },
      ],
      ///FECHAS
      inicio: null,
      menu: false,
      final: null,
      menu1: false,
      maximo: new Date().toISOString().substring(0,10)
    };
  },
  watch: {
    
  },
  methods: {
    ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),

    async Cargar_Datos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Registro");
      this.Modificar_Espera(true);
      this.loading = true;
      const json = JSON.stringify({
        token: localStorage.getItem("Token"),
        id: this.no_trabajador,
        fechaI:this.inicio,
        fechaF:this.final
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/Registro-Rango",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Correcta");
          
          const registros = [];
          const Meses = [
            "Enero",
            "Febrero",
            "Marzo",
            "Abril",
            "Mayo",
            "Junio",
            "Julio",
            "Agosto",
            "Septiembre",
            "Octubre",
            "Noviembre",
            "Diciembre",
          ];
          res.data.Datos.forEach((element) => {
            const tam = registros.length;
            registros.push({
              id: tam,
              No_empleado: element.No_Empleado,
              Nombre: element.Nombre + " " + element.ApellidoP,
              Fecha:
                element.Fecha.substring(8, 10) +
                " de " +
                Meses[parseInt(element.Fecha.substring(5, 7))-1] +
                " del " +
                element.Fecha.substring(0, 4),
              Hora: element.Hora,
              Tipo: element.Concepto === 1 ? "Entrada" : "Salida",
            });
          });
          this.Registros = registros;          
          this.Modificar_Boton(true);
          this.loading = false;
        } else if (res.data.Error) {
            
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);          
          this.Modificar_Datos("Sin Registros");          
          this.Modificar_Boton(true);
          this.loading = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);        
        this.loading = false;
      }
    },
  },
};
</script>